const initialState = {
    apotek:[],
    onloadingapotek:false,

    onloadingprovinsi:false,
    provinsi:[],

    product:[],
    onloadingproduct:false,

    productbyapotek:[],
    onloadingbyapotek:false,
  }

export const hullwemaReducer = (state=initialState,action)=>{
    switch(action.type){
        case 'ONLOADING_DATA_APOTEK':
            return{
                ...state,
                onloadingapotek:true
            }
        case 'GET_DATA_APOTEK':
            return{
                ...state,
                onloadingapotek:false,
                apotek:action.payload
            }
        case 'END_DATA_APOTEK':
            return{
                ...state,
                onloadingapotek:false
            }
        case 'ONLOADING_DATA_PROVINSI':
            return{
                ...state,
                onloadingprovinsi:true
            }
        case 'GET_DATA_PROVINSI':
            return{
                ...state,
                onloadingprovinsi:false,
                provinsi:action.payload
            }
        case 'END_DATA_PROVINSI':
            return{
                ...state,
                onloadingprovinsi:false
            }
        case 'ONLOADING_DATA_PRODUCT':
            return{
                ...state,
                onloadingproduct:true
            }
        case 'GET_DATA_PRODUCT':
            return{
                ...state,
                onloadingproduct:false,
                product:action.payload
            }
        case 'END_DATA_PRODUCT':
            return{
                ...state,
                onloadingproduct:false
            }            
        case 'ONLOADING_DATA_PRODUCT_APOTEK':
            return{
                ...state,
                onloadingbyapotek:true
            }
        case 'GET_DATA_APOTEK':
            return{
                ...state,
                onloadingbyapotek:false,
                productbyapotek:action.payload
            }
        case 'END_DATA_APOTEK':
            return{
                ...state,
                onloadingbyapotek:false
            }
        default:
            return{...state}
    }

}