import React from 'react'
import DataTable from 'react-data-table-component'
import { connect} from 'react-redux'
import { Link } from 'react-router-dom'
import Select from 'react-select'
import { Button, Card, CardBody, CardHeader, Col, Form, FormGroup, Label } from 'reactstrap'
import './apotek.css'
import { getDataApotek } from './store/actions'
import axios from 'axios'

const apiurl='http://localhost:4000/v1'

const options = [
    { value: 'chocolate', label: 'Toko1' },
    { value: 'strawberry', label: 'Toko2' },
    { value: 'vanilla', label: 'Toko3' }
  ]

const options2 = [
    { value: 'chocolate', label: 'Produk1' },
    { value: 'strawberry', label: 'Produk2' },
    { value: 'vanilla', label: 'Produk3' }
  ] 

  class Apotek extends React.Component{
    state ={
        dataApotek:[],
        onloadingapotek:false,
        dataProduct:[],
        onloadingproduct:false,
        dataPredict:[],
        onloadingpredict:false,
        idtoko:"",
        previd:"",
        idbarang:"",
        column :[
            {
                name:"Nama Barang",
                selector: "namabarang",
                sortable:true,
                minWidth:"150px",
                wrap:true
            },
            {
                name:"Stock",
                selector:"quantity",  
                cell: row=>(
                    <>
                        {row.quantity+" "+row.satuanstock}
                    </>
                )          
            },
            {
                name:"Hasil Prediksi",
                selector:"predict",
                cell: row=>(
                    <>
                        {Math.round(parseInt(row.predict))+" "+row.satuanstock}
                    </>
                )  
            },
            {
                name:"Saran Re-Stock",
                selector:"saran",
                cell: row=>(
                    <>
                        {
                            Math.round(parseInt(row.predict))-row.quantity<1?
                            "tidak perlu restock":
                            Math.round(parseInt(row.predict))-row.quantity+" "+row.satuanstock
                        }
                    </>
                )  
            }
        ]
    } 

    componentDidMount(){
        this.fetchtoko()
        // this.fetchproduct()
    }

    componentDidUpdate(){
        this.fetchproduct()
    }
    fetchtoko(){
        this.setState({onloadingapotek:true})
        axios({
            url:apiurl,
            method:'post',
            data:{
                query:`
                    query{
                        GetListApotekHullWEMA{
                        response{
                            errMsg,
                            status
                        },
                        toko{
                            idtoko,
                            namausaha
                            }
                        }
                    }
                `
            }
        }).then((response)=>{
            response.data.data.GetListApotekHullWEMA.toko.map(data=>{
                var matches = data.namausaha.match(/\b(\w)/g)
                data.namausaha=matches.join('')
            })
            this.setState({dataApotek:response.data.data.GetListApotekHullWEMA.toko,onloadingapotek:false})
        }).catch(error=>{
            console.log(error)
        })
    }

    fetchproduct(){
        if(this.state.idtoko!==""){
            if(this.state.previd!==this.state.idtoko){
                axios({
                    url:apiurl,
                    method:"post",
                    data:{
                        query:`
                            query{
                                GetListProductbyApotek(idapotek:${this.state.idtoko}){
                                response{
                                errMsg,
                                status
                                }
                                product{
                                    idbarang,
                                namabarang,
                                }
                            }  
                            }
                        `
                    }
                }).then((response)=>{
                    console.log(response)
                    this.setState({dataProduct:response.data.data.GetListProductbyApotek.product, previd:this.state.idtoko,onloadingproduct:false})
                }).catch((error)=>{
                    console.log(error)
                })
            }
        }
    }

    handleSubmit=()=>{
        if(this.state.idtoko===""){
            alert("pilih apotek")
        }else if(this.state.idbarang===""){
            alert("pilih barang")
        }else{
            this.setState({onloadingpredict:true})
            console.log(this.state.idtoko, this.state.idbarang)
            axios({
                url:apiurl,
                method:'post',
                data:{
                    query:`
                        mutation{
                            PredictionHullWEMAApotek(idapotek:${this.state.idtoko},idbarang:${this.state.idbarang}){
                                response{
                                    errMsg,
                                    status
                                },
                                    prediction{
                                    predict,
                                    namabarang,
                                    satuanstock,
                                    quantity
                                }
                            }
                        }
                    `
                }
            }).then((response)=>{
                console.log(response)
                this.setState({onloadingpredict:false, dataPredict:[response.data.data.PredictionHullWEMAApotek.prediction]})
            }).catch((error)=>{
                console.log(error)
            })
        }
    }

    handleChangeApotek=(e)=>{
        this.setState({idtoko:e.idtoko})
    }
    handleChangeProduct=(e)=>{
        this.setState({idbarang:e.idbarang})
    }
    render() {
      return(
        <div className='apotekbg'>
            <Card>
                <CardHeader>
                    Apotek
                    <Form>
                        <FormGroup>
                            <Label>
                                Pilih Apotek
                            </Label>
                            <Select
                                placeholder="pilih apotek"
                                name="apotek" 
                                options={this.state.dataApotek}
                                getOptionLabel={option=>(option.idtoko+" - "+option.namausaha)}
                                getOptionValue={option=>option.idtoko} 
                                onChange={val=>this.handleChangeApotek(val)}
                                className="react-select" 
                                classNamePrefix="select" 
                                isLoading ={this.state.onloadingapotek}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label>
                                Pilih Produk
                            </Label>
                            <Select 
                                options={this.state.dataProduct}
                                placeholder="pilih product"
                                name='product'
                                isDisabled={(!this.state.onloadingproduct&& this.state.dataProduct.length!==0 &&this.state.idtoko!=="")? false:true}
                                getOptionLabel={option=>(option.idbarang+" - "+option.namabarang)}
                                getOptionValue={option=>option.idbarang} 
                                onChange={val=>this.handleChangeProduct(val)}
                                className="react-select" 
                                classNamePrefix="select" 
                                isLoading ={this.state.onloadingproduct}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Button color="primary" type="button" onClick={()=>this.handleSubmit()}>
                                Simulasi
                            </Button>
                        </FormGroup>
                    </Form>
                </CardHeader>
                <CardBody>
                    <DataTable
                        columns={this.state.column}
                        data={this.state.dataPredict}
                        progressPending={this.state.onloadingpredict}
                        className="dataTable-custom"
                    />

                </CardBody>
            </Card>
            <Link to={"/"}>
                <Button style={{width:"300px", height:"50px"}} color='danger' className='mt-2'> 
                    Back
                </Button>
            </Link>
        </div>
      )
  }
}

const mapStateToProps=(state)=>
{
  return {
      hullwema: state.hullwema
  }
}

export default connect(mapStateToProps,{})(Apotek)
