import React, { useState } from 'react'
import { Briefcase, ShoppingCart } from 'react-feather'
import { Link } from 'react-router-dom'
import { Card, CardBody, CardHeader, Col, Tooltip } from 'reactstrap'
import '../App.css'
const Home=()=> {
    
  const [tooltipOpen1, setTooltipOpen1] = useState(false)
  const [tooltipOpen2, setTooltipOpen2] = useState(false)
  return (
    <div className='App'>
        <header className="App-header">
        <Card className='card'>
          <CardHeader>
            Welcome, Choose between Company And Toko Apotek
          </CardHeader>
          <CardBody>
            <Col md="12">
              <Link to={`/company`} id={'company'}>
                <Briefcase size={150} />
              </Link>
              <Link to={`/apotek`} id={'apotek'}>
                <ShoppingCart size={140} />
              </Link>
            </Col>
            <Tooltip
              placement='top'
              isOpen={tooltipOpen1}
              target={'company'}
              toggle={() => setTooltipOpen1(!tooltipOpen1)}
            >
              Company
            </Tooltip>
            <Tooltip
              placement='top'
              isOpen={tooltipOpen2}
              target={'apotek'}
              toggle={() => setTooltipOpen2(!tooltipOpen2)}
            >
              Apotek
            </Tooltip>
          </CardBody>
        </Card>
        </header>

    </div>
  )
}
export default Home 
