import React from 'react'
import DataTable from 'react-data-table-component'
import { connect} from 'react-redux'
import { Link } from 'react-router-dom'
import Select from 'react-select'
import { Button, Card, CardBody, CardHeader, Col, Form, FormGroup, Label } from 'reactstrap'
import './apotek.css'
import { getDataApotek } from './store/actions'
import axios from 'axios'

const apiurl='http://localhost:4000/v1'

class Company extends React.Component{
    state ={
        dataProvinsi:[],
        onloadingprovinsi:false,
        dataProduct:[],
        onloadingproduct:false,
        dataPredict:[],
        onloadingpredict:false,
        idprovinsi:"",
        previd:"",
        idbarang:"",
        column :[
            {
                name:"Nama Barang",
                selector: "namabarang",
                sortable:true,
                minWidth:"150px",
                wrap:true
            },
            {
                name:"Stock",
                selector:"quantity",  
                cell: row=>(
                  <>
                      {row.quantity+" "+row.satuanstock}
                  </>
              )              
            },
            {
                name:"Hasil Prediksi",
                selector:"predict",
                cell: row=>(
                  <>
                      {Math.round(parseInt(row.predict))+" "+row.satuanstock}
                  </>
              )  
            },
            {
                name:"Saran Re-Stock",
                selector:"saran",
                cell: row=>(
                  <>
                      {
                          Math.round(parseInt(row.predict))-row.quantity<1?
                          "tidak perlu restock":
                          Math.round(parseInt(row.predict))-row.quantity+" "+row.satuanstock
                      }
                  </>
              )
            }
        ]
    } 

    componentDidMount(){
        this.fetchprovinsi()
        // this.fetchproduct()
    }

    componentDidUpdate(){
        this.fetchproduct()
    }
    fetchprovinsi(){
        this.setState({onloadingapotek:true})
        axios({
            url:apiurl,
            method:'post',
            data:{
                query:`
                  query{
                    GetListProvinsi{
                      response{
                        errMsg,
                        status
                      },
                      provinsi{
                        idprovinsi,
                        namaprovinsi
                      }
                    }
                  }
                `
            }
        }).then((response)=>{
            this.setState({dataProvinsi:response.data.data.GetListProvinsi.provinsi,onloadingprovinsi:false})
        }).catch(error=>{
            console.log(error)
        })
    }

    fetchproduct(){
        if(this.state.idtoko!==""){
            if(this.state.previd!==this.state.idtoko){
                axios({
                    url:apiurl,
                    method:"post",
                    data:{
                        query:`
                          query{
                            GetListProduct{
                              response{
                                errMsg,
                                status
                              },
                              product{
                                idbarang,
                                namabarang
                              }
                            }
                          }
                        `
                    }
                }).then((response)=>{
                    console.log(response)
                    this.setState({dataProduct:response.data.data.GetListProduct.product, previd:this.state.idtoko,onloadingproduct:false})
                }).catch((error)=>{
                    console.log(error)
                })
            }
        }
    }

    handleSubmit=()=>{
        if(this.state.idtoko===""){
            alert("pilih apotek")
        }else if(this.state.idbarang===""){
            alert("pilih barang")
        }else{
            this.setState({onloadingpredict:true})
            console.log(this.state.idprovinsi, this.state.idbarang)
            axios({
                url:apiurl,
                method:'post',
                data:{
                    query:`
                        mutation{
                            PredictionHullWEMACompany(idprovinsi:${this.state.idprovinsi},idbarang:${this.state.idbarang}){
                                response{
                                    errMsg,
                                    status
                                },
                                    prediction{
                                    predict,
                                    namabarang,
                                    satuanstock,
                                    quantity
                                }
                            }
                        }
                    `
                }
            }).then((response)=>{
                console.log(response)
                this.setState({onloadingpredict:false, dataPredict:[response.data.data.PredictionHullWEMACompany.prediction]})
            }).catch((error)=>{
                console.log(error)
            })
        }
    }

    handleChangeProvinsi=(e)=>{
        this.setState({idprovinsi:e.idprovinsi})
    }
    handleChangeProduct=(e)=>{
        this.setState({idbarang:e.idbarang})
    }
    render() {
      console.log(this.state.dataPredict)
      return(
        <div className='apotekbg'>
            <Card>
                <CardHeader>
                    Apotek
                    <Form>
                        <FormGroup>
                            <Label>
                                Pilih Provinsi
                            </Label>
                            <Select
                                placeholder="pilih provinsi"
                                name="provinsi" 
                                options={this.state.dataProvinsi}
                                getOptionLabel={option=>(option.idprovinsi+" - "+option.namaprovinsi)}
                                getOptionValue={option=>option.idprovinsi} 
                                onChange={val=>this.handleChangeProvinsi(val)}
                                className="react-select" 
                                classNamePrefix="select" 
                                isLoading ={this.state.onloadingprovinsi}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label>
                                Pilih Produk
                            </Label>
                            <Select 
                                options={this.state.dataProduct}
                                placeholder="pilih product"
                                name='product'
                                isDisabled={(!this.state.onloadingproduct&& this.state.dataProduct.length!==0 &&this.state.idtoko!=="")? false:true}
                                getOptionLabel={option=>(option.idbarang+" - "+option.namabarang)}
                                getOptionValue={option=>option.idbarang} 
                                onChange={val=>this.handleChangeProduct(val)}
                                className="react-select" 
                                classNamePrefix="select" 
                                isLoading ={this.state.onloadingproduct}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Button color="primary" type="button" onClick={()=>this.handleSubmit()}>
                                Simulasi
                            </Button>
                        </FormGroup>
                    </Form>
                </CardHeader>
                <CardBody>
                    <DataTable
                        columns={this.state.column}
                        data={this.state.dataPredict}
                        progressPending={this.state.onloadingpredict}
                        className="dataTable-custom"
                    />

                </CardBody>
            </Card>
            <Link to={"/"}>
                <Button style={{width:"300px", height:"50px"}} color='danger' className='mt-2'> 
                    Back
                </Button>
            </Link>
        </div>
      )
  }
}

const mapStateToProps=(state)=>
{
  return {
      hullwema: state.hullwema
  }
}

export default connect(mapStateToProps,{})(Company)
